











































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import axios, { AxiosStatic, AxiosRequestConfig, AxiosPromise }  from 'axios'


@Component({name: "icon_select"})
export default class icon_select extends Vue {
  
    
    // init values
    icons_ready: number = 0
    mounted() {
      this.$store.commit('general/SET_ICONS')
    }

    link: number = 0;
                    
    private picture: Object =  this.$store.state.general.icon_images[this.$store.state.general.selected_icon_image];
    get map_value() {return this.picture}
    set map_value(value: Object) {this.picture = value}
   
    
    get icons() { 
        return this.$store.state.general.icons
    }
    
    
    @Watch('icons')
    on_change_icons(old_value: Array<Object>, new_value: Array<Object>){
      window.console.log('icons changed')
      window.console.log(this.$store.state.general.icons[0])
      window.console.log(this.$store.state.general.icons.length)
      this.icons_ready = 1
      return this.$store.state.general.icons
      
    }
   

    // customized icon
    get customized_icon() {
      return this.$store.state.general.customized_icon;
    }  
    @Watch('customized_icon')
    on_change_customized_icon(old_value: string, new_value: string){
      return this.$store.state.general.customized_icon;
    }



    // Function to put selected icon in store
    select_icon(id: number) {
      this.$store.commit('general/SELECTED_ICON', id)
      window.console.log('selected icon:' + this.$store.state.general.selected_icon)
    }


// Function create customized icons
    update_icon(): void{

        let payload: Object = {
            picture: this.$store.state.general.icon_images,
            icon: this.$store.state.general.icons[this.$store.state.general.selected_icon]
        }
        
        axios({
                method: 'post',
                url: this.$store.state.general.url_make_icon,
                data: payload
          }
            
          ).then(
            response => {
              window.console.log(response.data)
              this.$store.commit("general/CUSTOMIZED_ICON", response.data.image)
              window.console.log(this.$store.state.general.customized_icon)

            }

          )  
    }       


    // Upload image function
 /* This methods is used for uploading the image */
      uploadImage(event: Event) {

          // reset images
          let input: HTMLInputElement = event.target! as HTMLInputElement;
          let image = input.files![0];
          
          const reader = new FileReader();
          reader.readAsDataURL(image);

          reader.onload = (event: Event) => {
                  let uploaded_image = reader.result;
                  //console.log(this.image);
                  //window.console.log(uploaded_image);

                  this.$store.commit('general/ADD_TO_ICON_IMAGES', uploaded_image)

                  window.console.log('store:' + this.$store.state.general.icon_images)


                  // Render icon with image
                  this.update_icon();

                  
              };
      
            
        }
 


}
