








import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import axios, { AxiosStatic, AxiosRequestConfig, AxiosPromise }  from 'axios'
import { LMap, LTileLayer, LMarker, LGeoJson, LCircle, LTooltip, LIcon} from 'vue2-leaflet';


@Component({name: "action_buttons"})
export default class action_buttons extends Vue {
  
    // make mapp function
    make_map(): void {
        window.console.log('making map')
        let current_map = this.$store.state.general.map; 
        window.console.log(current_map)
        current_map.mapObject.getBounds().getNorthWest();
        
        window.console.log('center transformed: ' + current_map.center)    
        window.console.log('zoom: ' + current_map.mapObject.getZoom())

     let config: AxiosRequestConfig = {
      headers : {
      'Content-Type' : 'image/png'
      }
    }

    let payload: Object = {
      zoom: current_map.mapObject.getZoom(),
      NorthWest: current_map.mapObject.getBounds().getNorthWest(),
      SouthWest: current_map.mapObject.getBounds().getSouthWest(),
      NorthEast: current_map.mapObject.getBounds().getNorthEast(),
      SouthEast: current_map.mapObject.getBounds().getSouthEast(),
      lat: current_map.mapObject.getBounds().getNorthWest().lat,
      lng: current_map.mapObject.getBounds().getNorthWest().lng,
      markers: this.$store.state.general.markers,
      map: this.$store.state.general.current_tile.value,
      icon_images: this.$store.state.general.icon_images
    };
    
    
    window.console.log('payload for creating map');
    window.console.log(payload);

          axios({
                method: 'post',
                url: this.$store.state.general.url,
                data: payload
          }
            
          ).then(
            response => {
              window.console.log(response)

            }

          )
      
  }
    
    // Add marker function
    add_marker(): void{

        let payload: Object = 
        {
            id: this.$store.state.marker_counter,
            //iconUrl: 'http://leafletjs.com/examples/custom-icons/leaf-green.png',
            iconUrl:  this.$store.state.general.icons[this.$store.state.general.selected_icon].image,
            shadowUrl: this.$store.state.general.icons[this.$store.state.general.selected_icon].image,
            iconSize:     [150, 200], // size of the icon
            shadowSize:   [150, 200], // size of the shadow
            iconAnchor:   [22, 94], // point of the icon which will correspond to marker's location
            shadowAnchor: [4, 62],  // the same for the shadow
            popupAnchor:  [-3, -76], // point from which the popup should open relative to the iconAnchor
            coordinates: {'lat': this.$store.state.general.map_center[0], 'lng': this.$store.state.general.map_center[1]},
            title: 'id: ' + this.$store.state.general.marker_counter,
            tooltip: "id: " + this.$store.state.general.marker_counter
        }

        this.$store.commit('general/ADD_MARKER', payload)    
    }


}
