/*  -----------------------------------------------------------------------
                  FUNCTION TO GENERATE MAP 
----------------------------------------------------------------------- */
  // Making get request
  make_map(): void {
    let current_map = this.$refs.main_map as LMap; 
    current_map.mapObject.getBounds().getNorthWest();
        
    window.console.log('center transformed: ' + current_map.center)    
    window.console.log('zoom: ' + current_map.mapObject.getZoom())

     let config: AxiosRequestConfig = {
      headers : {
      'Content-Type' : 'image/png'
      }
    }

    let payload: Object = {
      zoom: current_map.mapObject.getZoom(),
      NorthWest: current_map.mapObject.getBounds().getNorthWest(),
      SouthWest: current_map.mapObject.getBounds().getSouthWest(),
      NorthEast: current_map.mapObject.getBounds().getNorthEast(),
      SouthEast: current_map.mapObject.getBounds().getSouthEast(),
      lat: current_map.mapObject.getBounds().getNorthWest().lat,
      lng: current_map.mapObject.getBounds().getNorthWest().lng,
      markers: this.$store.state.general.markers,
      map: this.$store.state.general.current_tile.value,
      icon_images: this.$store.state.general.icon_images
    };
    
    
    window.console.log('payload for creating map');
    window.console.log(payload);

          axios({
                method: 'post',
                url: this.$store.state.general.url,
                data: payload
          }
            
          ).then(
            response => {
              window.console.log(response)

            }

          )
      
  }

/* --------------------------------------------------------------------------------------
   Test google data:

-------------------------------------------------------------------------------------- */
  google_data(): void {

    //window.console.log(this.$store.state.general.example_data)
    let id: number = 4;
    let google_markers: Array<Object> =[];
    let icon_image: string = this.$store.state.general.icon_image;
    //window.console.log(icon_image)
    
    this.$store.state.general.example_data.forEach(function (value: any) {
            
//https://gis.stackexchange.com/questions/318918/latitude-and-longitude-values-in-google-takeout-location-history-data-sometimes/319067#319067
      
      let lat: number;
      let long: number;
      let date: Date = new Date(parseInt(value.timestampMs, 10));
      

      window.console.log(date)

      // Convert latitude and longitude
      if (value.latitudeE7 > 1800000000){
          lat = (value.latitudeE7 - 4294967296)/10000000
      }else {
          lat = (value.latitudeE7)/10000000
      }

      
      if (value.longitudeE7 > 1800000000){
          long = (value.longitudeE7 - 4294967296)/10000000
      }else {
          long = (value.longitudeE7)/10000000
      }     

    
      let m: Object = {
        id: id,
          //iconUrl: 'http://leafletjs.com/examples/custom-icons/leaf-green.png',
          //shadowUrl: 'http://leafletjs.com/examples/custom-icons/leaf-shadow.png',

          //iconUrl: require('@/assets/pics/christian.jpg'),
          //shadowUrl: require('@/assets/pics/christian.jpg'),


          iconUrl: icon_image,
          shadowUrl: icon_image,
          
          coordinates: [ lat, long],
          title: 'test',
          tooltip: date.toString()
          }
       
      id++;
      window.console.log(m)
      window.console.log('id:' + id)
      window.console.log(google_markers)
      
      google_markers.push(m)

    }); 

    this.$store.commit('general/UPDATE_GEO_DATA', google_markers)

    window.console.log(this.$store.state.general.markers)
  }
 



